body,
h1 {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,
    Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}

em {
  background: cyan;
  font-style: normal;
}

.header {
  display: flex;
  align-items: center;
  min-height: 50px;
  padding: 0.5rem 1rem;
  background-image: linear-gradient(to right, #8e43e7, #00aeff);
  color: #fff;
  margin-bottom: 1rem;
  font-family: "Malgun Gothic";
}

.header a {
  color: #fff;
  text-decoration: none;
}

.header-title {
  font-size: 1.5rem;
  font-weight: bolder;
  font-family: "Malgun Gothic";
}

.header-title::after {
  content: ' \25B8   ';
  padding: 0 0.5rem;
  font-size: 1.1rem;
}

.header-subtitle {
  padding-top: 0.25rem;
  font-size: 1.1rem;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.search-panel {
  display: flex;
}

.search-panel__filters {
  flex: 1 1;
}

.search-panel__results {
  flex: 3 1;
}

/*.searchbox {*/
/*  margin-bottom: 2rem;*/
/*}*/

.pagination {
  margin: 2rem auto;
  text-align: center;
}

.left-panel {
  float: left;
  width: 250px;
}

body { font-family: "Malgun Gothic"; padding: 1em; }
/*.ais-SearchBox { margin: 1em 0; }*/
/*.ais-Pagination { margin-top: 1em }*/

.right-panel { margin-left: 260px; }

.hit-name {
  margin-top: 0.5rem;
  font-weight: bolder;
  font-size:larger;
}
.hit-location {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  font-size:smaller;
  font-weight: lighter;
}
.hit-date {
  margin-bottom: 0.5em;
  font-size: smaller;
  font-weight: lighter;
}

.react-autosuggest__container {
  position: relative;
  margin-bottom: 1rem;
}

.react-autosuggest__input {
  width: 100%;
  box-sizing: border-box;
  padding: 20px 20px;
  font-family: "Malgun Gothic", sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  box-sizing: border-box;
  top: 51px;
  width: 100%;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: "Malgun Gothic", sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2;
}

.react-autosuggest__section-title {
  padding: 10px 20px;
  font-weight: 600;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}


